let index = 0;
const createArray = (size) => {
  return Array.from(Array(size).keys());
};
createArray(5); /* ? */
const piecesOr = createArray(11).map((card) => ({
  index: index++,
  type: "Pi√®ce d'or",
  value: 0.5
}));
const squelette = {
  index: index++,
  type: "squelette",
  value: 1
};
const cailloux = createArray(4).map((card) => ({
  index: index++,
  type: "Cailloux",
  value: 0
}));
const pierresPrecieuses = () =>
  createArray(7).map((card, idx) => ({
    index: index++,
    type: "Pierre pr√©cieuse",
    value: idx + 1
  }));

const cards = [
  ...piecesOr,
  ...cailloux,
  ...pierresPrecieuses(),
  ...pierresPrecieuses(),
  ...pierresPrecieuses(),
  ...pierresPrecieuses(),
  squelette
];

export default cards;
