import React, { useRef, useEffect } from "react";

import logo from "./logo.svg";
import "./App.css";

import { useState, useActions, useEffects, useReaction } from "./overmind";

import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";

import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import MenuIcon from "@material-ui/icons/Menu";

import IconButton from "@material-ui/core/IconButton";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import Fade from "@material-ui/core/Fade";

import Decks from "./Decks";

import socketIOClient from "socket.io-client";
const ENDPOINT = "http://localhost:8080";

const ITEM_HEIGHT = 48;

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1
  },
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3)
  }
}));

function App() {
  const [started, setStarted] = React.useState(false);
  const [response, setResponse] = React.useState("");
  const socketRef = useRef();
  const [modalOpen, setModalOpen] = React.useState(false);
  const [name, setName] = React.useState("");
  const classes = useStyles();
  const state = useState();
  const actions = useActions();

  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const me = state.players.find((p) => p.name === name);
  const amIPlaying = me?.isPlaying;
  const amIDealer = me?.isDealer;

  useEffect(() => {
    const socket = socketIOClient(ENDPOINT);
    socketRef.current = socket;
    socket.on("hello", (data) => {
      console.log("üöÄ ~ file: App.js ~ line 74 ~ socket.on ~ HELLO", data);
    });
    socket.on("FromAPI", (data) => {
      setResponse(data);
    });
    socket.on("emtyDeck", () => {
      console.log("emty deck");
    });
    socket.on("card", (card) => {
      console.log("üöÄ ~ file: App.js ~ line 80 ~ socket.on ~ card", card);
      console.log("card", card);
    });
    socket.on("newGame", (players) => {
      console.log("üöÄ ~ file: App.js ~ line 83 ~ socket.on ~ newGame", players);
      actions.setPlayers(players);
    });
    socket.on("players", (players) => {
      console.log("üöÄ ~ file: App.js ~ line 83 ~ socket.on ~ players", players);
      actions.setPlayers(players);
    });
    socket.on("playerAlreadyExist", (name) => {
      alert(`Le joueur ${name} existe d√©j√†`);
    });

    // CLEAN UP THE EFFECT
    return () => {
      socket.disconnect();
      socketRef.current = null;
    };
    //
  }, []);

  const handleNewGame = () => {
    setAnchorEl(null);
    socketRef.current.emit("newGame");
  };

  const handleChooseDealer = () => {
    setAnchorEl(null);
    socketRef.current.emit("chooseDealer");
  };

  const handlePlay = () => {
    setAnchorEl(null);
    setModalOpen(true);
  };

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleModalOpen = () => {
    setModalOpen(true);
  };

  const handleModalClose = () => {
    setModalOpen(false);
  };

  const handleEnterGame = () => {
    socketRef.current.emit("newPlayer", name);
    setStarted(true);
    setModalOpen(false);
  };

  const handleNewDuel = () => {
    // socketRef.current.emit("newCard", "ouverte");
  };
  const handleOuverte = () => {
    socketRef.current.emit("newCard", "ouverte");
  };
  const handleFerme = () => {
    socketRef.current.emit("newCard", "ferme");
  };
  const handleServi = () => {
    socketRef.current.emit("servi");
  };

  const handleChange = (event) => {
    setName(event.target.value);
  };

  return (
    <>
      <AppBar position="static">
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="menu"
            onClick={handleClick}>
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" className={classes.title}>
            Casino pirate
          </Typography>
        </Toolbar>
      </AppBar>

      <Menu
        id="fade-menu"
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
        TransitionComponent={Fade}>
        <MenuItem onClick={handleNewGame}>Nouvelle partie</MenuItem>
        <MenuItem onClick={handleChooseDealer}>Choisir dealer</MenuItem>
        <MenuItem onClick={handlePlay}>Jouer</MenuItem>
      </Menu>

      <Grid
        container
        direction="column"
        justify="center"
        alignItems="center"
        spacing={3}>
        {/* <img src={logo} className="App-logo" alt="logo" /> */}
        <Grid item style={{ width: "100%" }}>
          <Decks players={state.players} />
        </Grid>
        <Grid item style={{ width: "100%" }}>
          {`Cards : ${state.cards.length}`}
        </Grid>
        <Grid item>
          {amIDealer && (
            <Button variant="contained" color="primary" onClick={handleNewDuel}>
              Nouveau duel
            </Button>
          )}
          {amIDealer && (
            <Button variant="contained" color="primary" onClick={handleNewDuel}>
              Nouveau carte
            </Button>
          )}
          {!amIDealer && (
            <>
              <Button
                variant="contained"
                color="primary"
                onClick={handleOuverte}
                disabled={!amIPlaying}>
                Ouverte
              </Button>

              <Button
                variant="contained"
                color="primary"
                onClick={handleFerme}
                disabled={!amIPlaying}>
                Ferm√©e
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={handleServi}
                disabled={!amIPlaying}>
                Servi
              </Button>
            </>
          )}
        </Grid>

        <Dialog
          open={modalOpen}
          onClose={handleModalClose}
          aria-labelledby="form-dialog-title">
          {/* <DialogTitle id="form-dialog-title">Nom</DialogTitle> */}
          <DialogContent>
            <DialogContentText>Entrez votre nom.</DialogContentText>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Nom du joueur"
              type="text"
              fullWidth
              value={name}
              onChange={handleChange}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleModalClose} color="primary">
              Annuler
            </Button>
            <Button
              onClick={handleEnterGame}
              color="primary"
              disabled={name.length === 0}>
              Ok
            </Button>
          </DialogActions>
        </Dialog>
      </Grid>
    </>
  );
}

export default App;
